,[_vm._v("Los grupos focales son una herramienta poderosa en la investigación cualitativa, proporcionando una plataforma para la interacción y el intercambio de ideas entre participantes. Al comprender sus características claves, los investigadores pueden diseñar y llevar a cabo, grupos focales que generen datos significativos y aplicables. ")]),_vm._m(7),_c('div',{staticClass:"row mb-4"},[_vm._m(8),_c('div',{staticClass:"col-12 col-xl-7",attrs:{"data-aos":"fade-left"}},[_vm._m(9),_c('TarjetaAudio',{staticClass:"color-primario bg-white mb-3",attrs:{"texto":"Características de los grupos focales","tiempo":"","audio":require('../../assets/componentes/audios/Características de grupos focales_mezcla.mp3')},on:{"audio-hover":function($event){_vm.mostrarIndicadorTarjetaAudio = false}}})],1)]),_c('p',{staticClass:"mb-4"},[_vm._v("El proceso de un grupo focal es el siguiente:")]),_vm._m(10),_c('h2',[_vm._v("Aplicaciones de los grupos focales")]),_vm._m(11),_vm._m(12),_c('p',{staticClass:"mb-4"},[_vm._v("La observación como técnica de investigación cualitativa, ofrece una ventana única para comprender fenómenos complejos en contextos sociales. Su enfoque inmersivo, flexible y reflexivo, permite a los investigadores captar detalles que enriquecen la comprensión de las interacciones humanas. A través de descripciones enriquecidas y un enfoque holístico, la observación se convierte en una herramienta poderosa, para explorar y analizar dinámicas sociales.")]),_vm._m(13),_c('p',{staticClass:"mb-4"},[_vm._v("A continuación, se describen las características más importantes de la observación, como técnica de investigación cualitativa:")]),_c('div',{staticClass:"row mb-4"},[_c('div',{staticClass:"col-12 col-lg-7",attrs:{"data-aos":"fade-right"}},[_c('AcordionA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"div",attrs:{"titulo":"Inmersión en el contexto"}},[_c('p',{staticClass:"mb-4"},[_vm._v("La observación requiere que el investigador se sumerja en el entorno donde ocurre el fenómeno